import React, { useEffect, useState } from 'react';
import fetch from 'cross-fetch';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import InputAdornment from '@mui/material/InputAdornment';
import Link from '@mui/material/Link';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Tooltip from '@mui/material/Tooltip';
import Typography from '@mui/material/Typography';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import SaveAsIcon from '@mui/icons-material/SaveAs';
import UndoIcon from '@mui/icons-material/Undo';
import dateFormat from '@libs/dateFormat';
import qr from 'qrcode';

const ClientsPartnersTableRow = props => {
  const [initialName, setInitialName] = useState(props.clientsPartner.name);
  const [initialPartnerId, setInitialPartnerId] = useState(
    props.clientsPartner.partnerId
  );
  const [initialNote, setInitialNote] = useState(props.clientsPartner.note);
  const [uuid, setUuid] = useState(props.clientsPartner.uuid);
  const [name, setName] = useState(props.clientsPartner.name);
  const [partnerId, setPartnerId] = useState(props.clientsPartner.partnerId);
  const [note, setNote] = useState(props.clientsPartner.note);
  const [disabledUpdateButton, setDisabledUpdateButton] = useState(true);
  const [disabledUndoButton, setDisabledUndoButton] = useState(true);
  const [createdAt, setCreatedAt] = useState(props.clientsPartner.createdAt);
  const [updatedAt, setUpdatedAt] = useState(props.clientsPartner.updatedAt);
  const [createdBy, setCreatedBy] = useState(props.clientsPartner.createdBy);
  const [updatedBy, setUpdatedBy] = useState(props.clientsPartner.updatedBy);

  const [isOpenDialog, setIsOpenDialog] = useState(false);

  const [shareUrl, setShareUrl] = useState('');
  const [qrImage, setQrImage] = useState('');

  useEffect(() => {
    const _shareUrl = `${process.env.URL}invitations/?uuid=${props.clientsPartner.uuid}`;

    const f = async () => {
      try {
        const _qrImage = await qr.toDataURL(_shareUrl);
        setQrImage(_qrImage);
      } catch (err) {
        console.error(err);
      }
    };

    f();

    setShareUrl(_shareUrl);

    setInitialName(props.clientsPartner.name);
    setInitialPartnerId(props.clientsPartner.partnerId);
    setInitialNote(props.clientsPartner.note);
    setUuid(props.clientsPartner.uuid);
    setName(props.clientsPartner.name);
    setPartnerId(props.clientsPartner.partnerId);
    setNote(props.clientsPartner.note);
    setDisabledUpdateButton(true);
    setDisabledUndoButton(true);
    setCreatedAt(props.clientsPartner.createdAt);
    setUpdatedAt(props.clientsPartner.updatedAt);
    setCreatedBy(props.clientsPartner.createdBy);
    setUpdatedBy(props.clientsPartner.updatedBy);
    setIsOpenDialog(false);
  }, [
    props.clientsPartner.uuid,
    props.clientsPartner.name,
    props.clientsPartner.partnerId,
    props.clientsPartner.note,
    props.clientsPartner.createdAt,
    props.clientsPartner.updatedAt,
    props.clientsPartner.createdBy,
    props.clientsPartner.updatedBy,
    props.username
  ]);

  const handleChangeClientName = ({ target: { value } }) => {
    const isDiff =
      value !== initialName ||
      partnerId !== initialPartnerId ||
      note !== initialNote;

    setDisabledUpdateButton(!(value && isDiff));
    setDisabledUndoButton(!isDiff);
    setName(value);
  };

  const handleChangePartnerId = ({ target: { value } }) => {
    const isDiff =
      name !== initialName ||
      value !== initialPartnerId ||
      note !== initialNote;

    setDisabledUpdateButton(!(name && isDiff));
    setDisabledUndoButton(!isDiff);
    setPartnerId(value);
  };

  const handleChangeNote = ({ target: { value } }) => {
    const isDiff =
      name !== initialName ||
      partnerId !== initialPartnerId ||
      value !== initialNote;

    setDisabledUpdateButton(!(name && isDiff));
    setDisabledUndoButton(!isDiff);
    setNote(value);
  };

  const handleClickUpdateButton = async () => {
    try {
      const res = await fetch(
        `${process.env.API_URL}/api/partners/clients/${uuid}`,
        {
          method: 'PUT',
          body: JSON.stringify({
            name,
            partnerId,
            note,
            updatedBy: props.username
          })
        }
      );

      if (res.status >= 400) {
        throw new Error('Bad response /api/partners/clients');
      }

      const {
        name: clientName,
        partnerId: clientPartnerId,
        note: clientNote,
        createdAt: clientCreatedAt,
        updatedAt: clientUpdatedAt,
        createdBy: clientCreatedBy,
        updatedBy: clientUpdatedBy
      } = await res.json();

      setInitialName(clientName);
      setInitialPartnerId(clientPartnerId);
      setInitialNote(clientNote);

      setName(clientName);
      setPartnerId(clientPartnerId);
      setNote(clientNote);
      setDisabledUpdateButton(true);
      setDisabledUndoButton(true);
      setCreatedAt(clientCreatedAt);
      setUpdatedAt(clientUpdatedAt);
      setCreatedBy(clientCreatedBy);
      setUpdatedBy(clientUpdatedBy);
    } catch (e) {
      alert('error');
      console.log(e);
    }
  };

  const handleClickUndoButton = () => {
    setName(initialName);
    setPartnerId(initialPartnerId);
    setNote(initialNote);
    setDisabledUpdateButton(true);
    setDisabledUndoButton(true);
  };

  const handleClickDeleteButton = () => setIsOpenDialog(true);

  const handleClickDeleteOk = async () => {
    try {
      const res = await fetch(
        `${process.env.API_URL}/api/partners/clients/${props.clientsPartner.uuid}`,
        {
          method: 'DELETE'
        }
      );

      if (res.status >= 400) {
        throw new Error('Bad response /api/partners/clients');
      }

      props.reload();
      setIsOpenDialog(false);
    } catch (e) {
      console.log(e);
    }
  };

  const handleCloseDialog = () => setIsOpenDialog(false);

  return (
    <>
      <TableRow
        hover={true}
        selected={!disabledUpdateButton || !disabledUndoButton}
      >
        <TableCell>
          <Link href={shareUrl} target='_blank'>
            <img
              src={qrImage}
              style={{
                width: '100px',
                height: '100px'
              }}
            />
          </Link>
        </TableCell>
        <TableCell>
          <TextField
            value={name}
            onChange={handleChangeClientName}
            InputProps={{
              endAdornment: <InputAdornment position='end'>様</InputAdornment>
            }}
          />
        </TableCell>
        <TableCell>
          <Select value={partnerId} onChange={handleChangePartnerId}>
            {props.partners.map(
              ({ uuid, lastNameKanji, firstNameKanji }, i) => (
                <MenuItem key={i} value={uuid}>
                  {lastNameKanji} {firstNameKanji}
                </MenuItem>
              )
            )}
          </Select>
        </TableCell>
        <TableCell>
          <TextField value={note} multiline onChange={handleChangeNote} />
        </TableCell>
        <TableCell>
          <Stack direction='row' spacing={1}>
            <Tooltip title='このレコードの変更を保存する'>
              <span>
                <IconButton
                  color='primary'
                  aria-label='update'
                  size='large'
                  disabled={disabledUpdateButton}
                  onClick={handleClickUpdateButton}
                >
                  <SaveAsIcon fontSize='inherit' />
                </IconButton>
              </span>
            </Tooltip>
            <Tooltip title='このレコードの変更を元に戻す'>
              <span>
                <IconButton
                  aria-label='undo'
                  size='large'
                  disabled={disabledUndoButton}
                  onClick={handleClickUndoButton}
                >
                  <UndoIcon fontSize='inherit' />
                </IconButton>
              </span>
            </Tooltip>
            <Tooltip title='このレコードを削除する'>
              <IconButton
                color='error'
                aria-label='delete'
                size='large'
                onClick={handleClickDeleteButton}
              >
                <DeleteForeverIcon fontSize='inherit' />
              </IconButton>
            </Tooltip>
          </Stack>
        </TableCell>
        <TableCell>
          <Typography variant='body2'>
            {dateFormat(createdAt)} <small>作成 ({createdBy})</small>
          </Typography>
          <Typography variant='body2'>
            {dateFormat(updatedAt)} <small>更新 ({updatedBy})</small>
          </Typography>
        </TableCell>
      </TableRow>
      <Dialog
        open={isOpenDialog}
        onClose={handleCloseDialog}
        aria-labelledby='alert-dialog-title'
        aria-describedby='alert-dialog-description'
      >
        <DialogTitle id='alert-dialog-title'>
          このレコードの削除しますか？
        </DialogTitle>
        <DialogContent>
          <DialogContentText id='alert-dialog-description'>
            顧客名　: {initialName}
            <br />
            担当者　:{' '}
            {(() => {
              const partner = props.partners.filter(
                ({ uuid }) => uuid === initialPartnerId
              )[0];
              return `${partner.lastNameKanji} ${partner.firstNameKanji}`;
            })()}
            <br />
            メモ　　: {initialNote}
            <br />
            作成日時: {dateFormat(createdAt)} ({createdBy})
            <br />
            更新日時: {dateFormat(updatedAt)} ({updatedBy})
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>キャンセル</Button>
          <Button onClick={handleClickDeleteOk} autoFocus>
            削除する
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default ClientsPartnersTableRow;
